body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  color: #333;
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  overflow: auto;
  padding: 20px;
}

.hero, .features, .education, .contact, .form {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  background-color: rgba(63, 102, 143, 0.8); /* Light blue */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  color: white;
  margin-bottom: 20px;
}

.hero h1, .features h2, .education h2, .contact h2, .form h2 {
  margin-top: 0;
}

.features ul, .education ul {
  padding-left: 20px;
}

.features ul li, .education ul li {
  margin-bottom: 5px;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  font-size: 1.1rem;
}

input, textarea {
  width: 100%;
  padding: 10px;
  border: 2px solid #007BFF; /* Light blue */
  border-radius: 5px;
  font-size: 1rem;
  margin-top: 5px;
  background-color: white;
  color: #333;
}

textarea {
  resize: vertical;
}

button {
  padding: 10px 20px;
  font-size: 1rem;
  background-color: #4CAF50; /* Green */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}

.notification {
  margin-top: 20px;
  padding: 10px;
  border-radius: 5px;
  color: white;
  text-align: center;
  width: 100%;
}

.notification.success {
  background-color: green;
}

.notification.error {
  background-color: red;
}
